* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: #3d3d3d;
}

html {
  font-size: 15px;
}

body {
  background-color: #f1edd9;
}

a {
  text-decoration: none;
  color: inherit;
}

figure {
  width: fit-content;
  height: fit-content;
}

img {
  display: block;
  max-width: 100%;
}

.all{
  width: 100%;
  overflow: hidden;
}
.main {
  width: 100%;
  height: 100vh;
}

header {
  width: 100%;
  height: 100px;
  border-bottom: 1px solid #808080;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #f1edd9;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
}

header h1 {
  width: 10%;
  min-width: 150px;
  height: fit-content;
  margin-left: 5vw;
}

nav {
  margin-right: 5vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

nav a {
  margin: 0 2vw;
  color: #3d3d3d;
  font-weight: 700;
}

.wrap {
  width: 100%;
  height: auto;
  position: relative;
  top: 100px;
  margin-bottom: 20vh;
}

.wrap h2 {
  margin-top: 5vh;
  text-align: center;
}

.wrap .container {
  width: 90%;
  overflow: hidden;
  margin: 5vh auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 35vw);
  gap: 3vw;
}

.wrap .container .item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.wrap .container .item figure {
  width: 70%;
  height: fit-content;
}

.wrap .container .item .big {
  width: 50%;
}

.wrap .container .item .text {
  position: absolute;
  bottom: 5%;
  text-align: center;
}

.wrap .container .item .text h4 {
  margin-bottom: 1vh;
}

.wrap .container .item .text p:nth-child(2) {
  margin-bottom: 1vh;
}

.wrap .container .item .text p:last-child {
  font-size: 0.7rem;
}

.home {
  width: 100vw;
  height: auto;
  display: flex;
  justify-content: flex-end;
}

.fix {
  width: 60vw;
  height: 95vh;
  position: fixed;
  background-image: url(/public/bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  top: 100px;
  left: 0;
}

.fix:hover {
  filter: grayscale(70%);
  transition: 0.6s;
}

.contents {
  width: 40vw;
  height: auto;
}

.perfume {
  width: 90%;
  height: auto;
  margin: 3vw auto;
  margin-top: 12vh;
}

.perfume>p:nth-of-type(1) {
  font-size: 0.8rem;
  color: #808080;
  margin-bottom: 2vh;
  font-weight: 800;
}

.perfume h2 {
  margin-bottom: 2vh;
  color: #3d3d3d;
  font-weight: 500;
}

.perfume>p:last-of-type {
  margin-bottom: 2vh;
  width: 80%;
  font-size: .9rem;
  line-height: 1.5rem;
  color: #3d3d3d;
}

.mainp1 {
  display: flex;
  width: 100%;
  margin-bottom: 10vh;
}

.mainp1 video {
  width: 90%;
}

.peritem {
  width: 90%;
  height: auto;
  margin: 5vh auto;
  margin-bottom: 10vh;
}

.peritem>p:first-of-type {
  font-size: 0.8rem;
  color: #808080;
  margin-bottom: 2vh;
  font-weight: 800;
}

.peritem h2 {
  margin-bottom: 2vh;
  color: #3d3d3d;
  font-weight: 500;
}

.peritem>p:last-of-type {
  margin-bottom: 2vh;
  width: 80%;
  font-size: .9rem;
  line-height: 1.5rem;
  color: #3d3d3d;
}

.pitem {
  display: flex;
  height: 20vw;
  align-items: center;
  justify-content: space-between;
  margin-top: 10vh;
}

.pitem .p1 {
  display: flex;
  width: 48%;
  height: 20vw;
  text-align: center;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
}

.p1 figure img {
  width: 85%;
  margin: auto;
}

.p2 figure img {
  width: 85%;
  margin: auto;
}

.ptext {
  width: 100%;
}

.pitem .p2 {
  display: flex;
  width: 48%;
  height: 20vw;
  text-align: center;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
}

.p1 h4,
.p2 h4 {
  margin-bottom: 1vh;
  color: #3d3d3d;
}

.p1 p,
.p2 p {
  color: #808080;
  font-size: 0.8rem;
}

#hp {
  width: 90%;
  height: auto;
  margin: 5vh auto;
}

#hp>p:first-of-type {
  font-size: .8rem;
  color: #808080;
  font-weight: 800;
  margin-bottom: 2vh;
}

#hp>h2 {
  color: #3d3d3d;
  font-weight: 500;
  margin-bottom: 2vh;
}

#hp>p:last-of-type {
  margin-bottom: 2vh;
  width: 80%;
  font-size: .9rem;
  line-height: 1.5rem;
  color: #3d3d3d;
}

#hp .pitem .p2 figure {
  margin-top: 3vh;
}
#hp .pitem{
  margin-top: 10vw;
}
#hp .pitem:last-child {
  margin-bottom: 10vw;
}

#food {
  width: 90%;
  height: auto;
  margin: 5vh auto;
  margin-top: 15vh;
}

#food>h2 {
  margin-bottom: 2vh;
  color: #3d3d3d;
  font-weight: 500;
}

#food>p {
  margin-bottom: 2vh;
  width: 80%;
  font-size: .9rem;
  line-height: 1.5rem;
  color: #3d3d3d;
}

.mainp2 {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.mainp2 figure {
  width: 90%;
}

.pbtn {
  display: block;
  width: 70%;
  margin-left: 5%;
  background-color: transparent;
  border: 1px solid #a8a8a8;
  padding: 15px 0;
  margin-top: 2vh;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
  color: #3d3d3d;
  transition: .3s;
}

.pbtn:hover {
  background-color: #3d3d3d;
  color: #f1edd9;
}

#thank {
  width: 90%;
  height: auto;
  margin: 5vh auto;
  margin-top: 15vh;
}

#thank>p:first-of-type {
  font-size: .8rem;
  color: #808080;
  font-weight: 800;
  margin-bottom: 2vh;
}

#thank>h2 {
  color: #3d3d3d;
  font-weight: 500;
  margin-bottom: 2vh;
}

#thank>p:last-of-type {
  margin-bottom: 2vh;
  width: 80%;
  font-size: .9rem;
  line-height: 1.5rem;
  color: #3d3d3d;
}

#thank .pbtn {
  margin-top: 3vh;
  margin-left: 0;
  width: 80%;
}

.mainp3 {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-bottom: 10vh;
}

.mainp3 figure {
  width: 90%;
}

footer {
  width: 100%;
  height: 20vh;
  background-color: #2c2c2c;
  color: #fffef2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.flogo{
  width: 10%;
  min-width: 100px;
  height: fit-content;
  filter:invert(100%);
}

.about {
  width: 100%;
  height: auto;
  position: relative;
  top: 100px;
}

.about .story>h2 {
  color: #3d3d3d;
  font-weight: 500;
  margin-top: 7vh;
  margin-bottom: 2vh;
  text-align: center;
}

.about .story>figure {
  width: 100%;
  height: 40vw;
  margin-top: 5vh;

}

.about .story>figure img {
  width: 100%;
  max-height: 100%;
}

.storytext {
  width: 80%;
  height: auto;
  margin: 5vh auto;
  display: flex;
  justify-content: space-between;
}

.storytext>h2 {
  width: 40%;
  font-size: 2rem;
  font-weight: 500;
  line-height: 3rem;
}

.storytext>.pbox {
  width: 50%;
}

.storytext>.pbox p {
  font-size: 1.1rem;
  line-height: 2rem;
}

.storytext>.pbox p:first-child {
  margin-bottom: 5vh;
}

.storytext>.pbox p:nth-child(2) {
  margin-bottom: 5vh;
}

.storytext .pbtn {
  margin-left: 0;
}

.slide {
  width: 90%;
  height: 40vw;
  margin: 10vh auto;
}

.slide figure {
  width: 100%;
  height: 100%;
}

.slide figure img {
  width: 100%;
  max-height: 100%;
}


@media screen and (max-width:1200px) {

  .wrap .container {
    grid-template-rows: repeat(3, 42vw);
  }

}

@media screen and (max-width:900px) {
  html{
    font-size: 13px;
  }
  .pitem {
    flex-direction: column;
  }
  .pitem .p1{
    width: 90%;
    height: 30vw;
    margin: auto;
  }
  .pitem .p2{
    width: 90%;
    height: 30vw;
    margin: auto;
    margin-top: 15vw;
  }
  .peritem{
    height: 100vw;
  }
  #hp{
    height: 170vw;
  }
  #hp .pitem:last-child{
    margin-top: 60vw;
  }
}
@media screen and (max-width:600px) {
  .wrap .container {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(5, 60vw);
  }
  html{
    font-size: 12px;
  }
  .storytext {
    flex-direction: column;
  }
  .peritem{
    height: 70vh;
  }
  #hp{
    height: 180vw;
    margin-top: 10vh;
  }
  #hp .pitem:last-child{
    margin-top: 70vw;
  }
  .storytext>h2 {
    width: 100%;
    margin-bottom: 5vh;
  }
  #thank{
    margin-top: 10vh;
  }
  #thank .pbtn{
    width: 100%;
  }
  #food{
    margin-top: 20vh;
  }
  .storytext>.pbox {
    width: 100%;
  }
  .wrap .container .item .text {
    bottom: 0%;
  }
  .pbtn{
    width: 90%;
    margin:2vh auto;
  }
  .fix{
    background-position: 60%;
    width: 50vw;
  }
  .contents{
    width: 50vw;
  }
}

@media screen and (max-width:410px) {
  html{
    font-size: 13px;
  }
  .wrap .container {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(9, 100vw);
  }
  .fix{
    width: 20vw;
    background-position:none;
    background-size: cover;
  }
  .contents{
    width: 80vw;
  }
  #hp{
    height: 175vh;
    margin-top: 5vh;
  }
  
  .perfume{
    width: 95%;
    margin-top: 15vh;
  }
  .peritem{
    width: 95%;
    height: 100vh;
    margin: 5vh auto;
  }
  .pitem{
    height: 80vh;
    margin-top: 5vh;
  }
  .pitem .p1{
    height: 30vh;
    margin-top: 5vh;
  }
  .pitem .p2{
    height: 30vh;
    margin-top:5vh;
  }
  #food{
    width: 95%;
    margin-top: 12vh;
  }
  #thank{
    width: 95%;
  }
  header nav a{
    font-size: 1.2rem;
    margin: 0 3vw;
  }
  #thank>p:last-of-type{
    width: 100%;
  }
  #food>p{
    width: 100%;
  }
  #hp>p:last-of-type{
    width: 100%;
  }
  .peritem>p:last-of-type{
    width: 100%;
  }
  .perfume>p:last-of-type{
    width: 100%;
  }
  #hp .pitem:last-child{
    margin-top: 0;
  }
  #hp>p:first-of-type{
    font-size: 1.3rem;
  }
  #thank>p:first-of-type{
    font-size: 1.3rem;
  }
  .perfume>p:first-of-type{
    font-size: 1.3rem;
  }
  .peritem>p:first-of-type{
    font-size: 1.3rem;
  }
}